import React from "react";
import ChatBot from "react-simple-chatbot";
import { ThemeProvider } from "styled-components";

function CustomChatbot(props) {
  const config = {
    width: "360px",
    height: "500px",
    floating: true
  };

  const theme = {
    background: "white",
    fontFamily: "Arial, Helvetica, sans-serif",
    headerBgColor: "#00B2B2",
    headerFontColor: "#fff",
    headerFontSize: "25px",
    botBubbleColor: "#00B2B2",
    botFontColor: "#fff",
    userBubbleColor: "#fff",
    userFontColor: "#4c4c4c",
  };

  const steps = [
   
    {
      id: "Greet",
      message: "Let me know once you are done",
      trigger: "input 1"
    },
    {
      id: "input 1",
      user: true,
      trigger: "input 3"
    },
    {
      id: "input 3",
      message: "I hope you liked his message. Time to give you his gift fro you",
      trigger: "option 4"
    },
    {
      id: "option 4",
      options: [
        { value: true, label: "üéÅ", trigger:()=>{window.location='/birthday/celeb'} },
      ],
    },
  
  ];

  return (
    <ThemeProvider theme={theme}>
      <ChatBot steps={steps} {...config} />
    </ThemeProvider>
  );
}

export default CustomChatbot;