import React from "react";
import ChatBot from "react-simple-chatbot";
import { ThemeProvider } from "styled-components";

function CustomChatbot(props) {
  const config = {
    width: "360px",
    height: "500px",
    floating: true
  };

  const theme = {
    background: "white",
    fontFamily: "Arial, Helvetica, sans-serif",
    headerBgColor: "#00B2B2",
    headerFontColor: "#fff",
    headerFontSize: "25px",
    botBubbleColor: "#00B2B2",
    botFontColor: "#fff",
    userBubbleColor: "#fff",
    userFontColor: "#4c4c4c",
  };

  const steps = [
    {
      id: "Greet",
      message: "I am a Birthday Bot",
      trigger: "Ask Name"
    },
    {
      id: "Ask Name",
      message: "May i know your name your name?",
      trigger: "Waiting user input for name"
    },
    {
      id: "Waiting user input for name",
      user: true,
      trigger: "intro 1"
    },
    {
      id: "intro 1",
      message: "Hiii {previousValue}",
      trigger: "input 1"
    },
    {
      id: "input 1",
      user: true,
      trigger: "message 1"
    },
    {
      id: "message 1",
      message: "Milan made this me and this website for your birthday.",
      trigger: "message 1a"
    },
    {
      id:"message 1a",
      message:" I know he is awfully late, the guy is new to making this so excuse him :)",
      trigger:"input 2"
    },
    {
      id: "input 2",
      user: true,
      trigger: "message 2"
    },
    
    {
      id: "message 2",
      message:"Would like to see a message from Milan?",
      trigger:"option 1"
    },
    {
      id: "option 1",
      options: [
        { value: true, label: "Yes", trigger:()=>{window.location='/birthday/intro'} },
        { value: false, label: "No", trigger: "No" }
      ]
    },
    {
      id: "No",
      message: "Please see it the guy made a lot of effort",
      trigger: "option 2"
    },
    {
      id: "option 2",
      options: [
        { value: true, label: "Okay", trigger:()=>{window.location='/birthday/intro'} }],
    },
   
  ];

  return (
    <ThemeProvider theme={theme}>
      <ChatBot steps={steps} {...config} />
    </ThemeProvider>
  );
}

export default CustomChatbot;