import React, { Component } from 'react'
import TypeWriterEffect from 'typewriter-effect';
import {Card} from 'react-bootstrap'
import Bot2 from './chatbot2'
import './intro.css'

export default class Intro extends Component{
    render()
    {
        return(
            <div>
                     <div className="outside">
                <div className="text-danger text-center">
                    <TypeWriterEffect
                                
                        onInit={(typewriter)=>{
                            typewriter.typeString('<span style="font-size:20px;">I know I am late, Sorry üòÖ</span>')
                            .stop()
                            .start();
                            
                        }}
                                
                    ></TypeWriterEffect>
                </div>
                
                
                <Card className=" container mt-20 bg-danger  text-white">
                <TypeWriterEffect
                /*options={{
                    strings: ['Hello', 'World']
                    .stop(),
                    autoStart: true,
                    loop: false,
                }}*/
                    textStyle={{
                    fontFamily: 'Arial',
                    color: 'white',
                    fontWeight: 500,
                    fontSize: '30px',
                    }}
                    text-align="center"
                    cursorColor="red"
                    onInit={(typewriter) => {
                        typewriter
                        .pauseFor(6000)
                        .typeString( '<span style="font-size:40px">Hey Artika</span>')
                        .pauseFor(1000)
                        .typeString('<span style="font-size:30px"><br>I love your </span>')
            
                        .typeString('<span style="font-size:32px">NAME</span>')
                        .pauseFor(1000)
                        .deleteChars(4)
                        .typeString('<span style="font-size:32px">EYES</span>')
                        .pauseFor(1000)
                        .deleteChars(4)
                        .typeString('<span style="font-size:32px">SMILE</span>')
                        .pauseFor(1000)
                        .deleteChars(5)
                        .typeString('<span style="font-size:32px">LIPS</span>')
                        .pauseFor(1000)
                        .deleteChars(4)
                        .typeString('<span style="font-size:32px">CHEEKS</span>')
                        .pauseFor(1000)
                        .deleteChars(6)
                        .typeString('<span style="font-size:32px">NOSE</span>')
                        .pauseFor(1000)
                        .deleteChars(4)
                        .typeString('<span style="font-size:32px">HAIR</span>')
                        .pauseFor(1000)
                        .deleteChars(4)
                        .typeString('<span style="font-size:32px">JUST EVERYTHING!!!!</span>')
                        .typeString('<span style="font-size:30px"><br>Its a new year and a new beginning. Wish you all the the happiness and love you deserve. Always keeps smiling no matter how tough life goes because there is always a way and always a me. I will always be there for you to love you care for you and pamper you. You are cute little baby and always stay the same, never grow up :)<br></span>')
                        .typeString('<span style="font-size:30px">SADNESS</span>')
                        .pauseFor(1000)
                        .deleteChars(7)
                        .pauseFor(1000)
                        .typeString('<span style="font-size:30px">May god backspace your sadness, the way I did and I always try to do so<br>Happy Birthday Artika <3 <3 <3</span>')
                        .stop()
                        .start();                     
                    }}
                />
                
                </Card>
                
                
            </div>
            <Bot2/>
            </div>
           
     
        )
    }
}