import React, { Component } from 'react'
import './photo.css'
import src1 from'./img1.jpg'
import src2 from'./img2.jpg'
import src3 from'./img3.jpg'
export class photo extends Component {
    render() {
        return (
            <div className="border">
            <div class="wrapper">

            <div class="item">
              <div class="polaroid"><img src={src1} alt={"#"}></img>
                <div class="caption">HAPPY</div>
              </div>
            </div>
          
            <div class="item">
              <div class="polaroid"><img src={src2} alt={"#"}></img>
                <div class="caption">BIRTHDAY</div>
              </div>
            </div>
          
            <div class="item">
              <div class="polaroid"><img src={src3} alt={"#"}></img>
                <div class="caption">ARTIKA</div>
              </div>
            </div>
          
          </div>
          </div>
        )
    }
}

export default photo
