import React from 'react'
import "bootstrap/dist/css/bootstrap.min.css"; //Bootstrap helful for CSS
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import  Intro from './components/message'
import Celeb from './components/confetti';
import home from './components/home';



function App()  {

    return (
            <Router>
              <Switch>
              <div>
                <Route path="/birthday/intro" component={Intro}/>
                <Route path="/birthday/celeb" exact component={Celeb}/>
                <Route path="/" exact component={home}/>

              
              </div>
              </Switch>
            </Router>            

        
        
        

    );
  }

export default App
